/* eslint-disable */

export default (state) => {
  state.cleanTools = () => {
    state.bucket.style.background = state.COLORS.transparent;
    state.samepixels.style.background = state.COLORS.transparent;
    state.pencil.style.background = state.COLORS.transparent;
    state.picker.style.background = state.COLORS.transparent;
    state.eraser.style.background = state.COLORS.transparent;
    state.reset.style.background = state.COLORS.transparent;
  };

  state.setFps = (newFps) => {
    state.fps = newFps;
    if (state.animating) {
      state.play();
      state.play();
    }
    state.playPreview();
    document.getElementById('fps-range').value = state.fps;
    document.getElementById('fps-indicator').innerHTML = `${state.fps} fps`;
  };

  state.fpsChange = () => {
    state.setFps(document.getElementById('fps-range').value);
  };

  state.changeColor = (newColor) => {
    if (newColor !== state.color && state.newColor !== state.COLORS.transparent) {
      const prevColorBuf = state.color;
      state.color = newColor !== state.COLORS.transparent ? newColor : state.COLORS.white;
      state.prevColor = prevColorBuf;
    }
    state.prevInd.style.background = state.prevColor;
    document.getElementById('color-selector').value = state.color;
  };

  state.changeSecondaryColor = (newColor) => {
    if (newColor !== state.secondaryColor && state.newColor !== state.COLORS.transparent) {
      const prevColorBuf = state.secondaryColor;
      state.secondaryColor = newColor !== state.COLORS.transparent ? newColor : state.COLORS.white;
      state.prevColor = prevColorBuf;
    }
    state.prevInd.style.background = state.prevColor;
    state.secondaryColorSelector.value = state.secondaryColor;
  };


  // animation related
  state.animate = () => {
    state.currentFrame += 1;
    if (state.currentFrame === state.frames.length) {
      state.currentFrame = 0;
    }
    state.frames[state.currentFrame].draw();
  };

  state.play = () => {
    if (!state.animating) {
      state.animationIndervalId = setInterval(state.animate, 1000 / state.fps);
      state.animating = true;
      document.getElementById('play').innerText = 'Stop (A)';
    } else {
      clearInterval(state.animationIndervalId);
      state.animating = false;
      document.getElementById('play').innerText = 'Play (A)';
    }
  };

  state.full = () => {
    if (!state.fullScreenMode || window.innerHeight !== window.screen.height) {
      state.fullScreenMode = true;
      document.getElementById('palette-background').requestFullscreen();
    } else {
      document.exitFullscreen();
      state.fullScreenMode = false;
    }
  };


  state.animatePreview = () => {
    document.getElementById('preview').src = state.previews[state.previewFrame];
    state.previewFrame += 1;
    if (state.previewFrame >= state.previews.length) {
      state.previewFrame = 0;
    }
  };

  state.playPreview = () => {
    if (state.previewIndervalId) {
      clearInterval(state.previewIndervalId);
    }
    state.previewIndervalId = setInterval(state.animatePreview, 1000 / state.fps);
  };

  state.stopPreview = () => {
    if (state.previewIndervalId) {
      clearInterval(state.previewIndervalId);
    }
  };

  state.auth = () => {
    /* eslint-disable */
    // alert(gapi.load);
    var GoogleAuth;
    var SCOPE = 'https://www.googleapis.com/auth/drive.metadata.readonly';
    function handleClientLoad() {
      // Load the API's client and auth2 modules.
      // Call the initClient function after the modules load.
      gapi.load('client:auth2', initClient);
    }

    function initClient() {
      var GoogleAuth;
      var SCOPE = 'https://www.googleapis.com/auth/drive.metadata.readonly';
      function handleClientLoad() {
        // Load the API's client and auth2 modules.
        // Call the initClient function after the modules load.
        gapi.load('client:auth2', initClient);
      }

      function initClient() {
        // Retrieve the discovery document for version 3 of Google Drive API.
        // In practice, your app can retrieve one or more discovery documents.
        var discoveryUrl = 'https://www.googleapis.com/discovery/v1/apis/drive/v3/rest';

        // Initialize the gapi.client object, which app uses to make API requests.
        // Get API key and client ID from API Console.
        // 'scope' field specifies space-delimited list of access scopes.
        gapi.client.init({
            'apiKey': 'AIzaSyBiDcJoeDbNMJdO6_ni2h1K6TT3odmxjJY',
            'discoveryDocs': [discoveryUrl],
            'clientId': '617312604420-lmg08nnal3kk1jv3mopkep45vduqk39i.apps.googleusercontent.com',
            'scope': SCOPE
        }).then(function () {
          GoogleAuth = gapi.auth2.getAuthInstance();

          // Listen for sign-in state changes.
          GoogleAuth.isSignedIn.listen(updateSigninStatus);

          // Handle initial sign-in state. (Determine if user is already signed in.)
          var user = GoogleAuth.currentUser.get();
          setSigninStatus();

          // Call handleAuthClick function when user clicks on
          //      "Sign In/Authorize" button.
          document.getElementById('sign-in-or-out-button').onclick = function() {
            handleAuthClick();
            alert('button clicked');
          }
          // $('#revoke-access-button').click(function() {
          //   revokeAccess();
          // });
        });
      }

      function handleAuthClick() {
        if (GoogleAuth.isSignedIn.get()) {
          // User is authorized and has clicked 'Sign out' button.
          GoogleAuth.signOut();
        } else {
          // User is not signed in. Start Google auth flow.
          GoogleAuth.signIn();
        }
      }

      function revokeAccess() {
        GoogleAuth.disconnect();
      }

      function setSigninStatus(isSignedIn) {
        var user = GoogleAuth.currentUser.get();
        var isAuthorized = user.hasGrantedScopes(SCOPE);
        // if (isAuthorized) {
        //   $('#sign-in-or-out-button').html('Sign out');
        //   $('#revoke-access-button').css('display', 'inline-block');
        //   $('#auth-status').html('You are currently signed in and have granted ' +
        //       'access to this app.');
        // } else {
        //   $('#sign-in-or-out-button').html('Sign In/Authorize');
        //   $('#revoke-access-button').css('display', 'none');
        //   $('#auth-status').html('You have not authorized this app or you are ' +
        //       'signed out.');
        // }
      }

      function updateSigninStatus(isSignedIn) {
        setSigninStatus();
      }
    }

    handleClientLoad();

    // initClient();

  }
};
